import React from 'react';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderBack from "@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack";
import Group from "@vkontakte/vkui/dist/components/Group/Group";
import Header from "@vkontakte/vkui/dist/components/Header/Header";
import Cell from "@vkontakte/vkui/dist/components/Cell/Cell";
import Switch from "@vkontakte/vkui/dist/components/Switch/Switch";
import styles from "../styles/custom-styles.sass";
import Div from '@vkontakte/vkui/dist/components/Div/Div';

import RanksItem from './RanksItem';

const RankInfo = ({ ranks, go, id }) => {

    const renderRanks = ranks.map(item => {
        return (
            <RanksItem
                key={item.id}
                id={item.id}
                name={item.name}
                ratingMin={item.ratingMin}
                ratingMax={item.ratingMax}
                pillBlue={item.pillBlue}
                pillRed={item.pillRed}
                pillGold={item.pillGold}
                shirt={item.shirt}
                cell={item.cell}
                vacation={item.vacation}
            />
        );
    });



    return (
        <Panel id={id}>
            <PanelHeader left={<PanelHeaderBack onClick={go} data-to="home" />}>
                Звания в ДУРКЕ
            </PanelHeader>
            <Div>
                {renderRanks}
            </Div>

        </Panel>
    );
};

export default RankInfo;
