import React from 'react';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderBack from "@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack";
import Group from "@vkontakte/vkui/dist/components/Group/Group";
import Header from "@vkontakte/vkui/dist/components/Header/Header";
import Cell from "@vkontakte/vkui/dist/components/Cell/Cell";
import styles from "../styles/custom-styles.sass";
import Div from '@vkontakte/vkui/dist/components/Div/Div';


const HowItWorks = ({ go, id }) => {


    return (
        <Panel id={id}>
            <PanelHeader left={<PanelHeaderBack onClick={go} data-to="home" />}>
                Как работает ДУРКА
                </PanelHeader>
            <Div>
                <Group header={<Header mode="secondary">Как майнить DurkaCoins?</Header>}>
                    <Cell>
                        На главном экране нажмите кнопку "Привлечь шизоидов",
                        после чего появятся способы как это сделать.
                        За каждого пользователя, который перешел по вашей ссылке,
                        Вы получаете +5 к рейтингу и +1 DurkaCoin.
                    </Cell>
                </Group>
                <Group header={<Header mode="secondary">Как попасть в ТОП?</Header>}>
                    <Cell>
                        ТОП формируется на основе Вашего рейтинга. 
                        Чем больше Ваш рейтинг, тем выше Вы в ТОП-е.
                    </Cell>
                </Group>
                <Group header={<Header mode="secondary">Для чего нужен DurkaCoin?</Header>}>
                    <Cell>
                        DurkaCoin можно переводить любым пользователям 
                        и тратить на покупки в "Магазине".
                        Для ускорения развития в ДУРКЕ, Вы можете купить
                        DurkaCoins за реальные деньги. Для этого, перейдите
                        во вкладку "Профиль" и нажмите кнопку "Купить".
                    </Cell>
                </Group>
                <Group header={<Header mode="secondary">Где хранятся покупки из магазина?</Header>}>
                    <Cell>
                        Во вкладке "Профиль" есть кнопка "Открыть инвентарь",
                        при клике на нее у Вас отобразятся все товары, которые 
                        вы купили.
                    </Cell>
                </Group>
                <Group header={<Header mode="secondary">Использование купленных товаров</Header>}>
                    <Cell>
                        Открываете свой инвентарь, выбираете товар, который 
                        хотите использовать и нажиматете кнопку "Использовать".
                        Пилюли и отпуск можно использовать только на себе.
                        Смирительную рубашку и одиночную камеру можно использовать
                        на любом пользователе ДУРКИ. 
                    </Cell>
                </Group>
            </Div>
        </Panel>
    );
};

export default HowItWorks;
