import React from 'react';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Link from '@vkontakte/vkui/dist/components/Link/Link';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Header from '@vkontakte/vkui/dist/components/Header/Header';

import styles from "../styles/custom-styles.sass";
import Icon28MoneyTransferOutline from '@vkontakte/icons/dist/28/money_transfer_outline';
import Icon16Dropdown from '@vkontakte/icons/dist/16/dropdown';
import Icon12Fire from '@vkontakte/icons/dist/12/fire';

/**
 * @param {Object} params - Параметры
 * @param {number} params.id - Айди предмета
 * @param {string} params.name - Имя предмета
 * @param {number} params.ratingMin - Минимальный рейтинг
 * @param {number} params.ratingMax - Максимальный рейтинг
 * @param {number} params.pillBlue - Количество синих пилюль в неделю
 * @param {number} params.pillRed - Количество красных пилюль в неделю
 * @param {number} params.pillGold - Количество золотых пилюль в неделю
 * @param {number} params.shirt - Количество смирительных рубашек в неделю
 * @param {number} params.cell - Количество одиночных камер в неделю
 * @param {number} params.vacation - Количество отпусков в неделю
 */
const RanksItem = ({ id, name, ratingMin, ratingMax, pillBlue, pillRed, pillGold, shirt, cell, vacation }) => {

    return (
        <Group header={<Header mode="secondary">{name}</Header>}>
            <Cell className="rankCell"
                asideContent={`${ratingMin} - ${ratingMax || Infinity}`}
                // description={
                //     <Div className="div">
                //         <Div className="div">
                //             Получает {pillBlue}
                //         </Div>
                //         <Div className="div">
                //             Использовать
                //         </Div>
                //     </Div>
                // }
                indicator="Рейтинг "
            >
                {name.toLowerCase() === 'шизоид'
                    ?
                    <Div className="div">Ничего не получает.</Div>
                    :
                    <>
                        <Div className="div">Получает в неделю:</Div>
                        {pillBlue ? <Div className="div">Blue пилюль: {pillBlue} шт</Div> : ''}
                        {pillRed ? <Div className="div">Red пилюль: {pillRed} шт</Div> : ''}
                        {pillGold ? <Div className="div">Gold пилюль: {pillGold} шт</Div> : ''}
                        {vacation ? <Div className="div">Отпусков: {vacation} шт</Div> : ''}
                        {shirt ? <Div className="div">Смирительных рубашек: {shirt} шт</Div> : ''}
                        {cell ? <Div className="div">Одиночных камер: {cell} шт</Div> : ''}
                    </>
                }

            </Cell>

        </Group>
    );
};

export default RanksItem;

